import React from "react";
import { Col, Row, Button, Rate, Spin, Card, Modal } from "antd";
import { Link } from "react-router-dom";
import moment from 'moment';
import { LoadingOutlined } from '@ant-design/icons';
import { api_base_url, method_post, method, headers } from '../../util/config'

class Barbers extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            barber_data: [],
            isLoading: true,
            barber_id: '',
            viewDeleteModal: false,
        };
        this.handleActivateProfile = this.handleActivateProfile.bind(this);
        this.handleDeactivateProfile = this.handleDeactivateProfile.bind(this);
        this.handleDeleteCategory = this.handleDeleteCategory.bind(this);
    }

    componentDidMount() {
        this.fetchAllBarbers()
    }

    fetchAllBarbers() {
        fetch(api_base_url + "listAllBarber", method_post).then(response => response.json())
            .then(response => {
                this.setState({ barber_data: response.data, isLoading: false })
            })
            .catch();
    }

    handleActivateProfile(value) {
        this.setState({ isLoading: true })
        const requestOptions = {
            method,
            headers,
            body: JSON.stringify({ barber_id: value })
        }

        fetch(api_base_url + "activateBarberProfile", requestOptions)
            .then(() => { this.fetchAllBarbers() })
            .catch();
    }

    handleDeactivateProfile(value) {
        this.setState({ isLoading: true })
        const requestOptions = {
            method,
            headers,
            body: JSON.stringify({ barber_id: value })
        }

        fetch(api_base_url + "deactivateBarberProfile", requestOptions)
            .then(() => { this.fetchAllBarbers() })
            .catch();
    }

    deleteUser(value) {
        this.setViewDeleteModal(false)
        this.setState({ isLoading: true })
        const requestOptions = {
            method,
            headers,
            body: JSON.stringify({ barber_id: value })
        }
        fetch(api_base_url + "deleteBarber", requestOptions)
            .then(() => { this.fetchAllBarbers() })
            .catch();
    }
    handleDeleteCategory(value) {
        this.setState({ barber_id: value })
        this.setViewDeleteModal(true)
    }

    setViewDeleteModal(viewDeleteModal) { this.setState({ viewDeleteModal }) }
    render() {
        const { barber_data, barber_id, isLoading, viewDeleteModal } = this.state;
        const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />

        return (
            <div>
                <Row>
                    <Col span={24}>
                        <h2 className="title gx-mb-4">Barbers</h2>
                    </Col>

                    {isLoading ?
                        <Col span={24}>
                            <Spin indicator={antIcon} />
                        </Col>
                        :
                        <Col span={24}>
                            {barber_data.map((data, index) => (
                                <div className={`gx-user-list gx-card-list`}>
                                    <img alt="..." src={data.profile_picture} className="gx-avatar-img gx-border-0" />
                                    <div className="gx-description">
                                        <div className="gx-flex-row">
                                            <h4>ID : {data.id}</h4>
                                            <span className="gx-d-inline-block gx-toolbar-separator">&nbsp;</span>
                                            <h4> <Link to={`/barber_details/${data.id}`}>{data.full_name}</Link></h4>
                                            <span className="gx-d-inline-block gx-toolbar-separator">&nbsp;</span>
                                            <span>{data.email}</span>
                                            <span className="gx-d-inline-block gx-toolbar-separator">&nbsp;</span>
                                            <span>
                                                {data.barber_title}
                                            </span>
                                            <span className="gx-d-inline-block gx-toolbar-separator">&nbsp;</span>
                                            <span>{moment().diff(data.dob, 'years')}</span>
                                            <span className="gx-d-inline-block gx-toolbar-separator">&nbsp;</span>
                                            <span>Current Status :  {data.is_verified === 1 ? "Active" : "Not Active"}</span>
                                        </div>
                                        <p className="gx-text-grey gx-mb-2">{data.title}</p>
                                        {data.working_days.map((days, index) => (
                                            <span key={index} className="ant-badge gx-badge gx-badge-outline">{days}</span>
                                        ))}
                                        <br />
                                        {data.services.map((service, index) => (
                                            <span key={index} className="ant-badge gx-badge gx-badge-outline">{service}</span>
                                        ))}
                                        <p>
                                            <span className="gx-mr-3">
                                                <span className="gx-text-grey">
                                                    <Rate disabled defaultValue={data.rating} />
                                                </span>
                                            </span>
                                        </p>
                                    </div>
                                    <div className="gx-card-list-footer">
                                        {data.is_verified === 1 ?
                                            <Button type="danger" onClick={() => this.handleDeactivateProfile(data.id)}>Not Active</Button>
                                            :
                                            <Button type="primary" onClick={() => this.handleActivateProfile(data.id)} >Active</Button>
                                        }
                                        <Button type="danger" onClick={() => this.handleDeleteCategory(data.id)}>Delete</Button>
                                    </div>
                                </div>
                            ))}
                        </Col>
                    }
                </Row>
                 {/* View Delete Modal */}
                 <Modal
                 centered
                 visible={viewDeleteModal}
                 footer={null}
                 header={null}
                 onCancel={() => this.setViewDeleteModal(false)}>
                 <p className="gx-text-grey gx-mb-2">Are you sure you want to delete <span style={{fontStyle:'inherit', fontSize:15, color:'black'}} >{barber_id}</span>?</p>
                 <div className="gx-card-list-footer">
                     <Button type="primary" onClick={() => this.deleteUser(barber_id)}>Yes</Button>
                     <Button onClick={() => this.setViewDeleteModal(false)}>No</Button>
                 </div>

             </Modal>
            </div>
                
        );
    }
};

export default Barbers;
